          currentPrecipitation = Math.round(myJSON.precipitation) + "%";
          currentWind = Math.round(myJSON.wind) + " mph";
          currentTime = myJSON.time;
          document.querySelector("span.temperature").innerHTML = currentTemp + "<sup>&#8457;</sup>";
